<template>
    <div>
    <div class="hot">
        周末
    </div>
    <ul>
        <li class="item border-bottom" 
        v-for="(item,index) of list" :key="index" >
            <div class="item-img">
                <img class="img-city" :src=item.imgUrl alt="">
            </div>
            <div class="detail">
                <p class="item-title">{{item.title}}</p>
                <p class="item-desc">{{item.desc}}</p>
               
            </div>
        </li>
    </ul>
    
    </div>
</template>

<script>
export default {
    name: 'weekend',
    props:{
        list: Array
    },
    data() {
        return {
          
        }
    },
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'
    .hot
     
     line-height:.8rem
     background:#eee
     text-indent:.2rem
    .item-img
     height:0
     overflow:hidden
     padding-bottom:33.9%
     .img-city
      width:100%
    
    .item-title
     line-height:.54rem
     font-size:.32rem
     ellipsis()
    .item-desc
     line-height:.4rem
     color:#ccc
     ellipsis()
   





</style>



