<template>
    <div>
    <div class="search">
        <input v-model="keyword" class="searchinput" type="text" placeholder="输入城市">
    
    
    </div>
    <div class="search-content" ref="search" v-show="keyword">
        <ul>
            <li class="searchsize border-bottom" 

            @click="hand(item.name)"
            v-for="item of list">
               {{item.name}}
            </li>
            <li class="searchsize border-bottom" v-show="!list.length">没有</li>
        </ul>

    </div>
    </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
    name: 'search',
    props:{
        cities:Object
    },
     methods: {
     ...mapMutations(['changecity']),
        hand (city) {
          console.log(city)
          this.changecity(city)
           this.$router.push('/')
        }
    },
    data() {
        return {
            keyword: '',
            list:[],
             timer: null
        }
    },
     watch: {
        keyword () {
            if(this.timer) {
                clearTimeout(this.timer)
            }
            if(!this.keyword){
                this.list=[]
                return 
            }
            this.timer=setTimeout(()=>{
                const result= []
                for (let i in this.cities){
                    this.cities[i].forEach((value)=>{
                        if(value.spell.indexOf(this.keyword) > -1 ||
                        value.name.indexOf(this.keyword) > -1){
                            result.push(value)
                        }
                    })
                }
                this.list=result
            },100)
        }
    },
    mounted() {
        this.scroll= new Bscroll(this.$refs.search)
    },
}
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl';
    .search-content
     position:absolute
     overflow:hidden
     top:1.58rem 
     left:0
     right:0
     bottom:0
     background:#fff
     z-index:1
     .searchsize
      line-height:.62rem
      padding-left:.2rem
      color:#666
      
      
    
    
    .search
     height 0.72rem
     padding:0 .1rem
     background:$bgColor
     .searchinput
      box-sizing:border-box
      padding:0 .1rem
      width:100%
      height:.62rem
      line-height:.62rem 
      text-align:center
      border-radius:.06rem
      color:#666
</style>



