<template>
   <ul class="list">
       <li
        class="item" v-for="(item,key) of cities" 
        :key="key"
        @click="handleclick"
        
        >
           {{key}}

           </li>
       
  
   </ul>
</template>

<script>
export default {
    name: 'Alphabet',
    props:{
        cities:Object
    },
    data(){
        return {
            touchustatus:false
        }
    },
    computed: {
        letters () {

        }
    },
    methods: {
        handleclick(e){
            console.log(e.target.innerText);
            this.$emit('change',e.target.innerText);
        }
    },
}
</script>

<style lang="stylus" scoped> 
    @import '~styles/varibles.styl'
    .list
     position:absolute
     top:1.58rem
     right:0
     bottom:0
     width:.4rem
    
     display:flex
     flex-direction:column
     justify-content:center
     .item
      line-height:.44rem 
      text-align:center
      color:$bgColor


   
</style>



