<template>
   <div class="list" ref="wrapper">
       <div>
       <div class="area">
           <div class="title bordertopbottom">当前城市</div>
            <div class="button-list">
                <div class="buttonwrap" >
                <div class="button">{{this.$store.state.city}}</div>
                </div>
                
                
               
            </div>
       </div>
       <div class="area">
           <div class="title bordertopbottom">热门城市</div>
           <div class="button-list">
                <div class="buttonwrap" 
                
                v-for="item of hotcities" :key="item.id"
                @click="hand(item.name)"
                >
                <div class="button">{{item.name}}</div>
                </div>
              
              
            </div>
       </div>
      
       
       <div class="area"  v-for="(item,key) of cities" :key="key"
       :ref="key"
       >
           <div class="title bordertopbottom">{{key}}</div>
            <div class="item-list">
                <div class="item border-bottom" 
                @click="hand(inneritem.name)"
                v-for="inneritem of item" :key="inneritem.id">
                    {{inneritem.name}}
                </div>
               

            </div>
       </div>
       </div>
   </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
    name: 'list',
    props:{
        cities:Object,
        hotcities:Array,
        letter:String
    },
    computed: {
        ...mapState(['city'])
    },
    methods: {
        ...mapMutations(['changecity']),
        hand (city) {
          console.log(city)
          this.changecity(city)
           this.$router.push('/')
        }
    },
    mounted() {
        this.scroll= new Bscroll(this.$refs.wrapper)
        
    },
    watch: {
        letter() {
            console.log(this.letter);
            if(this.letter){
                const element=this.$refs[this.letter][0]
                this.scroll.scrollToElement(element)
                console.log(element)
            }
        }
    }
}
</script>

<style lang="stylus" scoped> 
    @import '~styles/varibles.styl'
    .bordertopbottom
     &:before
       border-color:#ccc
     &:before
       border-color:#ccc
   .list
    top:1.58rem
    left:0
    right:0
    bottom:0
    position:absolute
    overflow:hidden
   
    .title
     line-height:.44rem
     background:#eee
     padding-left:.2rem
     color:#666
     font-size:.26rem
    .button-list
     padding:.1rem .6rem .1rem .1rem
     overflow:hidden
     
     .buttonwrap
      float:left
      width:33.33%
      .button
       margin:.1rem
       padding: .1rem 0
       text-align:center;
       border:.02rem solid #ccc
       border-radius:.06rem
    .item-list
     .item
      line-height:.76rem
     
      padding-left:.2rem
    .border-bottom
      &:before
        border-color:#ccc
</style>



