<template>
    <div>
    <div class="hot">
        推荐
    </div>
    <ul>
        <li class="item border-bottom" 
        v-for="(item,index) of list" :key="index" >
            <div class="item-img">
                <img class="img-city" :src=item.imgUrl alt="">
            </div>
            <div class="detail">
                <p class="item-title">{{item.title}}</p>
                <p class="item-desc">{{item.desc}}</p>
                <button class="item-button">查看详情</button>
            </div>
        </li>
    </ul>
    
    </div>
</template>

<script>
export default {
    name: 'recommand',
    props:{
        list: Array
    },
    data() {
        return {
           
        }
    },
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'
    .hot
     margin-top:.2rem
     line-height:.8rem
     background:#eee
     text-indent:.2rem
    .item
     overflow:hidden
     display:flex
     height 1.9rem
     .img-city
      width:1.7rem
      height 1.7rem
      padding:.1rem
     .detail
      flex:1
      padding 0.1rem
      .item-title
       line-height:.54rem
       font-size:.32rem
       ellipsis()
      .item-desc
       line-height:.4rem
       color:#ccc
       ellipsis()
      .item-button
       margin-top:.2rem
       background #ff9300
       padding:0 .2rem
       border radius 0.06rem
       color:#fff





</style>



