<template>
    <div class="icons">
        <swiper :options="swiperOption">
        <swiper-slide v-for="(page,index) of page" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
            <div class="icon-img">
            <img class="img-size" :src='item.imgUrl' alt="">
            </div>
            <p class="font">{{item.desc}}</p>
            </div>
        </swiper-slide>
       
            </swiper>
        
    </div>
</template>

<script>
export default {
    name: 'homeicon',
    props:{
        list: Array
    },
    data () {
        return {
          swiperOption:{
              autoplay:false
          }
        }
    },
 computed: {
    page () {
         const pages= []
         this.list.forEach((item,index) =>{
             const page =Math.floor(index/8)
             if(!pages[page]){
                 pages[page] =[]
             }
             pages[page].push(item)
         })
         return pages;
    }
}
}

</script>

<style lang="stylus">
@import '~styles/mixins.styl'
    .icons
     overflow:hidden
     padding-bottom:50%
     height:0
     
     .icon
      position:relative
      float:left
      width:25%
      padding-bottom:25%
      
      .icon-img
       overflow:hidden
       position:absolute
       top:0
       left:0
       right:0
       bottom 0.44rem
       box-sizing:border-box
       padding:.1rem
       .img-size
        display:block
        margin:0 auto
        height:100%
      .font
       position:absolute
       left:0
       right:0
       bottom:0
       height 0.44rem
       line-height:.44rem
       color:#333
       text-align:center
       ellipsis()
      

</style>



