<template>
    <div class="banner">
        
        <img class="banner-img" src="http://img1.qunarzz.com/sight/p0/1809/10/10d6568ad3ad4bb5a3.img.jpg_r_800x800_6eb5edf5.jpg" alt="">
        <div class="banner-info">
            <div class="banner-title">五卅的但是</div>
            <div class="banner-number">
                <span class="iconfont">&#xe692</span>
            39
            </div>
        </div>
    <picture></picture>
    </div>
</template>
<script>
import picture from 'common/gallary/gallary.vue'
export default {
    name:'banner',
    components:{
        picture
    }
}
</script>

<style lang="stylus" scoped>
    .banner
     position:relative
     overflow:hidden
     height:0
     padding-bottom:55%
     .banner-img
      width:100%
     .banner-info
      display:flex
      position:absolute
      left:0
      right:0
      bottom:0
      color:#fff
      line-height:.6rem 
      background-image: linear-gradient(top,rgba(0,0,0,0),
      rgba(0,0,0,0.8))
      .banner-title
       flex:1
       font-size:.32rem
       padding:0 .2rem
      .banner-number
       padding:0 .4rem
       height:.32rem
       line-height:.4rem  
       border-radius 0.2rem
       background:rgba(0,0,0, .8)

      
</style>



